    
/* Google Fonts */

@import url('https://fonts.googleapis.com/css?family=Quicksand:400,500,700&display=swap');

body {
  margin: 0;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.content {
  margin: 0;
  overflow: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/* Begin Nav Bar */

.navbar {
  margin-top: 20px;
}

.navbar-inverse .navbar-nav>li>a {
  color: #fff;
  font-size: 15px;
}

.navbar-inverse .navbar-nav>.active>a, .navbar-inverse .navbar-nav>.active>a:focus, .navbar-inverse .navbar-nav>.active>a:hover {
  color: #01acb4;
  font-size: 20px;
}

.navbar-collapse {
  margin-left: 55px;
  background-color: #212930;
  border-color: #212930;
}

.navbar-inverse {
  background-color: #212930;
  border-color: #212930;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  border-radius: 0px;
  width: 100%;
  height: 100%;
  padding-bottom: 8px;
}

.navbar-collapse {
  padding-left: 55px;
}

.donate {
  background-color: #fff442;
  border-color: #fff442;
  color: #333;
  margin: 7px 40px 0px 0px;
  position: relative;
  float: right;
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
}

.navbar-inverse .navbar-nav>.active>a, .navbar-inverse .navbar-nav>.active>a:focus, 
.navbar-inverse .navbar-nav>.active>a:hover {
  background-color: transparent;
}

.navbar-toggle {
  float: left;
}

.navbar-collapse, .navbar-header {
  position: absolute;
}


/* End Nav Bar */

/* Begin Description */

.first-description {
  margin-top: 0px; 
}

.second-description {
  margin-top: 10px; 
}

/* End Description */

/* Begin News */

.button-news {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  color: #fff;
  background-color: #336384;
  border-color: #336384;
  padding: 10px;
  border-radius: 5px;
}

 #video {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	height: 0;
}

#video iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 90%;
	height: 90%;
}

#post {
	position: relative;
	padding-bottom: 96.25%; /* 16:9 */
	height: 0;
}
#post blockquote {
	position: absolute;
	top: 0;
	left: 0;
  width: 90%;
}

.button-youtube {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  color: #fff;
  background-color: #b93049;
  border-color: #b93049;
  padding: 10px;
  float: right;
  border-radius: 5px;
}

/* End News */
